<template>
  <div>
    <h3 v-if="product">{{product.name}}</h3>
    <h3 v-else>Product Name</h3>

    <table class="table" v-if="product">
      <tr>
        <th>Description</th>
        <td>:</td>
        <td>{{product.description}}</td>
      </tr>
      <tr>
        <th>Stock Price</th>
        <td>:</td>
        <td>{{product.stock_price | toIdr}}</td>
      </tr>
      <tr>
        <th>Categories</th>
        <td>:</td>
        <td>
          <el-tag
            style="margin:5px"
            v-for="category in product.categories" :key="category.category_id"
            type="info">{{category.name}}</el-tag>
        </td>
      </tr>
      <tr>
        <th>Prices</th>
        <td>:</td>
        <td>
          <el-tag
            style="margin:5px"
            v-for="price in product.prices" :key="price.price_id"
            type="info">{{price.type}} : {{price.price | toIdr}}</el-tag>
        </td>
      </tr>
      <tr>
        <th>Shops</th>
        <td>:</td>
        <td>
          <el-tag
            style="margin:5px"
            v-for="shop in product.shops" :key="shop.shop_id"
            type="info">{{shop.shop_name}}</el-tag>
        </td>
      </tr>
    </table>

    <el-row :gutter="8">
      <el-col :xs="{span: 24}" :sm="{span: 24}" :md="{span: 24}" :lg="{span: 8}" :xl="{span: 8}">
        <h4>Profit</h4>
        <app-line-chart
          :chart-data="profitStatistic"
          v-if="profitStatistic !== null"
        ></app-line-chart>
      </el-col>
      <el-col :xs="{span: 24}" :sm="{span: 24}" :md="{span: 24}" :lg="{span: 8}" :xl="{span: 8}">
        <h4>Transaction</h4>
        <app-line-chart
          :chart-data="transactionStatistic"
          v-if="transactionStatistic !== null"
        ></app-line-chart>
      </el-col>
      <el-col :xs="{span: 24}" :sm="{span: 24}" :md="{span: 24}" :lg="{span: 8}" :xl="{span: 8}">
        <h4>Stock</h4>
        <app-line-chart
          :chart-data="stockStatistic"
          v-if="stockStatistic !== null"
        ></app-line-chart>
        <div v-else>
          No Data
        </div>
      </el-col>
    </el-row>
  </div>
</template>


<script>
import LineChart from "@/components/Chart/LineChart";

export default {
  components: {
    appLineChart: LineChart,
  },
  data() {
    return {
      loading: false,

      profitStatistic: null,
      transactionStatistic: null,
      stockStatistic: null,
      product: null,
    };
  },
  methods: {
    getProductStatistic() {
      this.$store
        .dispatch("FetchProductStatistic", this.$route.params.id)
        .then(res => {
          const data = res.data;
          const messages = data.messages;
          const status_code = data.status_code;
          // this.profitStatistic = data.data.statistic.profit_statistic;
          this.profitStatistic = {
            chartData: data.data.statistic.profit_statistic,
            field: "profit",
          };
          this.transactionStatistic = {
            chartData: data.data.statistic.transaction_statistic,
            field: "transaction",
          };
          this.stockStatistic = {
            chartData: data.data.statistic.stock_statistic,
            field: "stock",
          };
        });
    },
    getProductDetail() {
      this.loading = true;
      this.$store
        .dispatch("FetchProduct", this.$route.params.id)
        .then(res => {
          const data = res.data;
          const messages = data.messages;
          const status_code = data.status_code;
          this.product = data.data.product;
          this.loading = false;
        });
    }
  },
  created() {
    this.getProductStatistic();
    this.getProductDetail();
  }
};
</script>

<style lang="scss">
.table {
  width: 100%;

  tr {
    th, td {
      text-align: left;
      padding: 10px;
      border-bottom: 1px solid #eee;
    }
    td {

    }
  }
}
</style>




